<template>
  <div class="box" id="box" style=" background: #f6f6f6;">
    <van-nav-bar :title="title" fixed left-arrow @click-left="onClickLeft"></van-nav-bar>
    <div class="communication_box">
      <div class="communication_box_pro"  @click="toProDetail(card.proid)">
        <img :src="card.pro_pic" alt="">
        <div class="videoDetail_pro_detail">
          <p>{{card.pro_introduce}}</p>
          <p>
            <span>{{card.clinic_name}}</span>
            <span>{{card.distance + 'km'}}</span>
          </p>
          <p>
            <span>{{'￥' + card.pro_price}}</span>
            <span>{{card.subscribe + '预约'}}</span>
          </p>
        </div>
      </div>
      <div class="communication_box_chat">
        <div :class="item.message_type === 'erchant' ? 'communication_chat_erchant' : 'communication_chat_user'" v-for="(item, index) in dataList.message" :key="index">
          <img :src="item.via" alt="">
          <p>
            <span v-if="item.flag">
              <h1 v-for="(itm, idx) of item.merchant_message" :key="idx" @click.once="huida(itm.name)">
                {{itm.name}}
              </h1>
            </span>
            <span v-else>{{item.merchant_message}}</span>
            <van-rate v-model="value" allow-half void-icon="star" void-color="#eee" v-if="item.star" @change="onChange"/>
          </p>
        </div>
      </div>
    </div>
     <div  style="position:fixed;bottom:0;">
       <div class="communication_need">
      <p>我想</p>
      <span @click="question">诊所的具体位置在哪里呢</span>
      <span @click="question">怎么预约呢</span>
      <span @click="question">结束</span>
    </div>
    <div v-if="yuyinShowBool" class="communication_footer">
      <span @click="toJianpan">
        <img src="@/assets/images/yuyin.png" alt="">
      </span>
      <input type="text" :value="communicationText" placeholder="请输入" @change="changeCommunicationText">
      <span @click="qita()">
        <img src="@/assets/images/qita.png" alt="">
      </span>
    </div>
    <div v-else class="communication_footer">
      <span @click="toYuyin">
        <img src="@/assets/images/jianpan.png" alt="">
      </span>
      <button>按住说话</button>
      <span @click="qita()">
        <img src="@/assets/images/qita.png" alt="">
      </span>
    </div>
    <div v-if="qitaFlag" class="other">
       <ul class="otherimg">
         <li>
           <img src="@/assets/images/paizhao.png" alt=""/>
           <p>拍照</p>
         </li>
         <li>
           <img src="@/assets/images/xiangce.png" alt=""/>
           <p>图片</p>
         </li>
       </ul>
    </div>
     </div>
  </div>
</template>

<script>
import Vue from 'vue'
import { NavBar, Icon, Divider, Rate } from 'vant'
import { getProductById } from '@/api'
Vue.use(NavBar)
Vue.use(Icon)
Vue.use(Divider)
Vue.use(Rate)
export default {
  data () {
    return {
      arr1: [],
      arr: [],
      value: 2.5,
      people: '',
      address: '',
      qitaFlag: false,
      card: [],
      proid: '',
      title: '',
      communicationText: '',
      yuyinShowBool: true,
      dataList: {
        id: '',
        title: '',
        message: [
          {
            message_type: 'erchant',
            via: '',
            merchant_message: '您好亲爱的，有什么可以帮助你的呢'
          }
        ]
      }
    }
  },
  mounted () {
    // console.log(this.dataList)
    const servicerid = this.$route.params.servicerid
    // console.log(servicerid)
    this.proid = servicerid.split('=')[1]
    // console.log(this.proid)
    getProductById({
      proid: this.proid,
      username: localStorage.getItem('userTel_AM')
    }).then(res => {
      // console.log(res)
      if (res.data.code === '200') {
        this.card = res.data.data[0]
        this.dataList.message[0].via = this.card.pro_pic
        this.dataList.id = this.proid
        // console.log(this.card)
        this.address = this.card.clinic_address
        this.people = this.card.subscribe
        this.title = this.card.clinic_name
        this.dataList.title = this.title
      }
    })
    // this.dataList = JSON.parse(localStorage.getItem('service'))
    this.cun()
  },
  methods: {
    fd () {
      this.dataList.message.push({
        message_type: 'erchant',
        via: this.card.pro_pic,
        merchant_message: '感谢您的评价，下次再见'
      })
      this.cun()
    },
    fc () {
      if (this.communicationText === ' 是') {
        this.dataList.message.push({
          message_type: 'erchant',
          via: this.card.pro_pic,
          merchant_message: '稍等，正在为您转接'
        })
      } else if (this.communicationText === ' 否') {
        this.dataList.message.push({
          message_type: 'erchant',
          via: this.card.pro_pic,
          merchant_message: '正在为您取消'
        })
      } else if (this.communicationText === ' 已解决') {
        this.dataList.message.push({
          message_type: 'erchant',
          via: this.card.pro_pic,
          merchant_message: '请为本次服务评价星级',
          star: true
        })
        setTimeout(this.fd, 2000)
      } else if (this.communicationText === ' 未解决') {
        this.dataList.message.push({
          message_type: 'erchant',
          via: this.card.pro_pic,
          merchant_message: '感谢您的回答，我们将努力改进'
        })
      }
      this.cun()
    },
    fb () {
      this.dataList.message.push({
        message_type: 'erchant',
        via: this.card.pro_pic,
        flag: true,
        merchant_message: [
          { name: '1. 是' },
          { name: '2. 否' }
        ]
      })
      this.cun()
    },
    fa () {
      if (this.communicationText === '诊所的具体位置在哪里呢') {
        this.dataList.message.push({
          message_type: 'erchant',
          via: this.card.pro_pic,
          merchant_message: this.address
        })
      } else if (this.communicationText === '怎么预约呢') {
        this.dataList.message.push({
          message_type: 'erchant',
          via: this.card.pro_pic,
          merchant_message: '目前预约人数' + this.people + '人，我们会尽快为你安排'
        })
      } else if (this.communicationText === '结束') {
        this.dataList.message.push({
          message_type: 'erchant',
          via: this.card.pro_pic,
          flag: true,
          merchant_message: [
            { name: '你的问题是否已经解决' },
            { name: '1. 已解决' },
            { name: '2. 未解决' }
          ]
        })
      } else {
        this.dataList.message.push({
          message_type: 'erchant',
          via: this.card.pro_pic,
          merchant_message: '不太懂你说什么，是否需要转接人工？'
        })
        setTimeout(this.fb, 1000)
      }
      this.cun()
    },
    onChange (value) {
      // console.log(this.value)
    },
    huida (name) {
      this.communicationText = name.split('.')[1]
      // console.log(this.communicationText)
      this.dataList.message.push({
        message_type: 'user',
        via: 'https://m.juooo.com/static/img/logo-user.8413cbf.png',
        merchant_message: this.communicationText
      })
      setTimeout(this.fc, 1000)
    },
    question (e) {
      // console.log(e.target.innerText)
      this.communicationText = e.target.innerText
      this.dataList.message.push({
        message_type: 'user',
        via: 'https://m.juooo.com/static/img/logo-user.8413cbf.png',
        merchant_message: this.communicationText
      })
      setTimeout(this.fa, 1000)
    },
    qita () {
      this.qitaFlag = !this.qitaFlag
    },
    toProDetail (proid) {
      this.$router.push({ path: '/proDetail', query: { proid } })
    },
    onClickLeft () {
      this.$router.go(-1)
    },
    changeCommunicationText (e) {
      this.communicationText = e.target.value
      // console.log(this.communicationText)
      this.dataList.message.push({
        message_type: 'user',
        via: 'https://m.juooo.com/static/img/logo-user.8413cbf.png',
        merchant_message: this.communicationText
      })
      setTimeout(this.fa, 1000)
    },
    toJianpan () {
      this.yuyinShowBool = false
    },
    toYuyin () {
      this.yuyinShowBool = true
    },
    // 将聊天记录存在本地
    cun () {
      // if (localStorage.getItem('service') === null) {
      //   localStorage.setItem('service', JSON.stringify(this.arr))
      // } else {
      //   // 将聊天界面最后内容存于arr
      //   this.arr.splice(0, 1, {
      //     id: this.dataList.id,
      //     title: this.dataList.title,
      //     message: this.dataList.message
      //   })
      //   localStorage.setItem('service', JSON.stringify(this.arr))
      // }
    }
  }
}
</script>

<style lang="scss" scoped>
