  width: 100%;
  /* height: auto; */
  // min-height: 100%;
  background: #f6f6f6;
  margin-top: 0.46rem;
  // margin-bottom: 0.7rem;
  padding: 0.3rem 0.2rem;
  .communication_box_pro {
    width: 3.35rem;
    height: 1.22rem;
    padding: 0.2rem 0.15rem;
    background: #fff8f8;
    border-radius: 0.04rem;
    margin-bottom: 0.2rem;
    /* position: fixed;
    bottom: 0.85rem;
    z-index: 20; */
    box-shadow:0px 1px 4px 2px rgba(150, 150, 150, 0.2);
    img {
      float: left;
      margin-right: 0.1rem;
      width: 0.8rem;
      height: 0.8rem;
      border-radius: 0.05rem;
    }
