        margin-bottom: 0.04rem;
      }
      p:nth-of-type(2) {
        width: 100%;
        height: 0.14rem;
        line-height: 0.14rem;
        font-size: 0.12rem;
        color: #787777;
        margin: 11px 0 11px 0;
        span:nth-of-type(1) {
          float: left;
          max-width: 80%;
          margin-right: 0.1rem;
          overflow: hidden;
          text-overflow:ellipsis;
          white-space: nowrap;
        }
        span:nth-of-type(2) {
          line-height: 0.14rem;
          float: right;
        }
      }
      /* p:nth-of-type(3) {
        width: 100%;
