        font-size: 0.12rem;
        color: #9a9898;
        overflow: hidden;
        text-overflow:ellipsis;
        white-space: nowrap;
        margin-bottom: 0.04rem;
      } */
      p:nth-of-type(3) {
        width: 100%;
        height: 0.14rem;
        line-height: 0.14rem;
        span:nth-of-type(1) {
          float: left;
          color: #ff7070;
          font-size: 0.14rem;
        }
        span:nth-of-type(2) {
          float: right;
          color: #939191;
          font-size: 0.12rem;
        }
      }
    }
  }
  .communication_box_chat {
    width: 100%;
    height: 100%;
    .communication_chat_erchant {
      overflow: hidden;
      width: 100%;
